@import '../../mixins';

.container,
.join,
.staff {
	width: 80% !important;
	margin-left: 10%;
}

.logistics {
	display: flex;
	margin-top: 1.1rem;
	@include mobile {
		display: none;
	}

	.detail {
		display: flex;
		align-items: center;
		margin-right: 2rem;

		img {
			height: 2.3rem;
			margin-right: 0.75rem;
			@include mobile {
				width: 1rem;
			}
		}

		h5 {
			font-family: Rubik;
			font-weight: 500;
			font-size: 20px;
			text-transform: uppercase;
			color: black;
			// @include mobile {
			// 	font-size: 15px !important;
			// 	display: none;
			//  }
		}
	}
}

.logistics-mobile {
	display: none !important;
	margin-top: 1.1rem;
	@include mobile {
		display: flex !important;
		margin-left: 0 !important;
	}

	.detail {
		display: flex;
		align-items: center;
		// margin-right: 2rem;
		img {
			height: 2.3rem;
			margin-right: 20px;
			@include mobile {
				height: 2rem;
			}
		}

		h5 {
			font-family: Rubik;
			font-weight: 500;
			font-size: 20px;
			text-transform: uppercase;
			color: black;
		}
	}
}

.description {
	@include text;
	margin-top: 1.2rem;
	margin-bottom: 1.5rem;
}

.divider-header {
	@include subtitle;
	font-size: 30px;
	margin-top: 2rem;
	margin-bottom: 20px;
}

.join {
	margin-top: 4.5%;
}

.technologies {
	position: relative;

	.blobs {
		position: absolute;
		width: 100%;
		height: 125%;
		top: 0;
		margin-top: -8%;
		margin-left: -7%;
		z-index: 1;
		@include mobile {
			width: 160%;
			z-index: inherit;
		}
	}
}

.stack {
	@include box;
	display: flex;
	flex-direction: column;
	align-items: center;
	position: relative;
	margin-top: 9%;
	padding-bottom: 0;

	h5 {
		@include subtitle;
		margin-bottom: 1.7rem;
	}

	.branch {
		height: 7rem;
		margin-bottom: 4rem;
		max-width: 100%;
		@include mobile {
			height: 3rem;
		}
	}
}

.staff {
	position: relative;
	margin-top: 9.5%;

	h4 {
		@include subtitle;
		margin-bottom: 1.7rem;
		z-index: 2;
	}
}
