@import '../../../mixins';
.container {
	display: flex;
	height: 8vh;

	.logo {
		position: absolute;
		left: 10px;
		height: 2rem;
	}

	.navbar {
		position: absolute;
		right: 0;
		width: 20%;
		height: 4.5vh;
		z-index: 2;
		// background: rgba(255, 255, 255, 0.6);
		// border: 1.4px solid #5d76fe;
		border-right: none;
		border-bottom-left-radius: 1000px;
		border-top-left-radius: 1000px;
	}

	.lines {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: space-around;

		img {
			height: 70%;
			width: 70%;
		}
	}

	.nav-container {
		// width: 200%;
		// height: 100px;
		position: absolute;
		top: 6vh;
		right: 1rem;
		// display: none;
	}

	// .navbar:hover {
	// 	.nav-container {
	// 		display: block;
	// 	}
	// }

	.nav {
		position: relative;
		width: fit-content;
		height: 100%;
		display: flex;
		flex-direction: column;
		padding: 1rem 1.2rem 0.5rem 1.2rem;
		background: rgba(255, 255, 255, 0.6);
		backdrop-filter: blur(13.0883px);
		border: 1px solid #ffffff;
		border-radius: 0.7rem;
		z-index: 100;

		.nav-link {
			@include nav-item;
			display: flex;
			align-items: center;
			font-size: 14px;
			text-transform: uppercase;
			white-space: nowrap;
			margin-bottom: 0.4rem;
		}

		.arrow {
			height: 7px;
			margin-left: 0.5rem;
		}

		.dropdown-link {
			margin-left: 1rem;
		}
	}
}
